import React from 'react';

import {
  Accordion,
  AccordionItem,
  AccordionItemHeading,
  AccordionItemButton,
  AccordionItemPanel,
} from 'react-accessible-accordion';

// Demo styles, see 'Styles' section below for some notes on use.
import 'react-accessible-accordion/dist/fancy-example.css';

export default function AccordionGokcin() {
  return (
    <Accordion allowMultipleExpanded={true} allowZeroExpanded={true}>
      <em>
      Salkl Beslenme ve Fitoterapi Uzman.
      </em>
      <AccordionItem>
        <AccordionItemHeading>
          <AccordionItemButton>Eitim</AccordionItemButton>
        </AccordionItemHeading>
        <AccordionItemPanel>
        Bursa Atatrk Anadolu Lisesi mezunudur. Beslenme ve Diyetetik eitimini 2016 ylnda Yeditepe niversitesinde tamamlamtr. 
        Lisans Tezi: Migren Tedavisinde Beslenme.
        2018 ylnda Fitoterapi Yksek Lisansn Yeditepe niversitesi Eczaclk Fakltesinde tamamlamtr. Yksek Lisans Bitirme Projesi: Emzirme Dneminde Kullanlan St Artrc Bitkiler.          
        </AccordionItemPanel>
      </AccordionItem>
      <AccordionItem>
        <AccordionItemHeading>
          {' '}
          <AccordionItemButton> Deneyimi</AccordionItemButton>{' '}
        </AccordionItemHeading>
        <AccordionItemPanel>
          <p>
            <ul>
              <li>
              2018-2019 PoziFit Beslenme
              </li>
              <li>
              2017-2018 zel Doruk Bursa Hastanesi
              </li>
              <li>
              2017 Anadolu Salk Merkezi
              </li>
              <li>2017 Medhome Laboratuvar Grntleme Merkezinde Adidas Merkezinde iyeri diyetisyeni olarak almtr.</li>
            </ul>
          </p>
        </AccordionItemPanel>
      </AccordionItem>
      <AccordionItem>
        <AccordionItemHeading>
          {' '}
          <AccordionItemButton>Mesleki Eitimler / Sertifikalar</AccordionItemButton>{' '}
        </AccordionItemHeading>
        <AccordionItemPanel>
          <p>
            <ul>
              <li>
              Psikodiyet Workshop
              </li>
              <li>
              V.Ulusal Salkl Yaam Sempozyumu
              </li>
              <li>Briatrik Cerrahi Alannda Diyetisyen Rol</li>
              <li>Karbonhidrat Saym</li>
              <li> Sal ve Gvenlii Eitimi </li>
              <li>IV. Ulusal Salkl Yaam Sempozyumu</li>
              <li>
              Anoreksiyadan Obeziteye Yeme Bozukluklar
              </li>
              <li>Acbadem Salkl Yaam Gnleri: Spor ve Beslenme</li>
            </ul>
          </p>
        </AccordionItemPanel>
      </AccordionItem>
    </Accordion>
  );
}
